html {
  height: 100%;
}

:root {
  --header-height: 60px;
  --base-color: #fff;
  --blue-color: #00acdc;
  --text-color: #000;
}

body {
  color: var(--text-color);
  height: 100%;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  line-height: 14px;
}

.header {
  background-color: var(--base-color);
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

a, a:visited, a:hover, a:active {
  color: inherit;
  text-decoration: none;
}

.logo__img {
  width: 40px;
  height: 40px;
  display: flex;
}

.menu__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.menu__item {
  margin: 0 10px;
}

.menu__item:first-child {
  margin-left: 0;
}

.menu__item:last-child {
  margin-right: 0;
}

.menu__link {
  text-transform: uppercase;
  height: var(--header-height);
  justify-content: center;
  align-items: center;
  font-weight: 500;
  display: flex;
}

.menu__link.is-active {
  color: var(--blue-color);
  position: relative;
}

.menu__link.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.menu__link:hover {
  color: var(--blue-color);
}

